<template>
  <div class="game-board-view">
    <div class="game-container">
      <h1 class="game-title">Game Board</h1>
      <div class="current-phase">{{ gameStore.currentPhase }}</div>
      
      <div class="game-board">
        <div class="game-board__sidebar">
          <div class="player-status">
            <h2>Player Status</h2>
            <div class="status-item">
              <span class="status-label">Health:</span>
              <span class="status-value">{{ playerStore.health }}</span>
            </div>
            <div class="status-item">
              <span class="status-label">Character:</span>
              <span class="status-value">{{ currentCharacter?.name || 'Unknown' }}</span>
            </div>
            <div class="status-item">
              <span class="status-label">Current Season:</span>
              <span class="status-value">{{ formatSeason(gameStore.currentSeason) }}</span>
            </div>
          </div>
          
          <div class="game-log">
            <h2>Game Log</h2>
            <div class="log-entries">
              <div v-for="(entry, index) in gameStore.gameLog" :key="index" 
                   class="log-entry" :class="{ 'highlighted': entry.highlight }">
                {{ entry.message }}
              </div>
            </div>
          </div>
        </div>
        
        <div class="game-board__main">
          <div class="game-board__phase-content">
            <div class="landscape" v-if="currentLandscape">
              <h2>Current Landscape: {{ currentLandscape.name }}</h2>
              <p>{{ currentLandscape.description }}</p>
              
              <div class="landscape-challenge" v-if="gameStore.currentChallenge">
                <h3>Challenge</h3>
                <p>{{ currentChallenge?.description }}</p>
                <button class="action-btn" @click="resolveChallenge" 
                        :disabled="!canResolveChallenge">
                  Resolve Challenge
                </button>
              </div>
              
              <div class="landscape-resources">
                <h3>Available Resources</h3>
                <div class="resource-list">
                  <div v-for="resource in availableResources" :key="resource.id" class="resource-item">
                    <span>{{ resource.name }}</span>
                    <button class="gather-btn" @click="gatherResource(resource)" 
                            :disabled="!canGatherResources">
                      Gather
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          
          <div class="game-controls">
            <button class="control-btn" @click="nextPhase">Next Phase</button>
            <button class="control-btn danger" @click="endJourney">End Journey</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import { usePlayerStore } from '@/stores/playerStore';
import { useGameStore } from '@/stores/gameStore';
import { useCardStore } from '@/stores/cardStore';
import { useGameState } from '@/composables/useGameState';
import { GamePhase } from '@/models/enums/phases';
import { Season } from '@/models/enums/seasons';
import type { ResourceCard, ChallengeCard } from '@/models/types/cards';

// Initialize router and stores
const router = useRouter();
const playerStore = usePlayerStore();
const gameStore = useGameStore();
const cardStore = useCardStore();
const { 
  currentCharacter, 
  currentLandscape, 
  canResolveChallenge 
} = useGameState();

// Computed properties
const availableResources = computed(() => {
  if (!currentLandscape.value?.availableResources) return [];
  return currentLandscape.value.availableResources
    .map(id => cardStore.getResourceById(id))
    .filter((resource): resource is ResourceCard => resource !== undefined);
});

const currentChallenge = computed(() => {
  if (!gameStore.currentChallenge) return null;
  return cardStore.getChallengeById(gameStore.currentChallenge);
});

const canGatherResources = computed(() => {
  return gameStore.currentPhase === GamePhase.RESOURCE_MANAGEMENT &&
         playerStore.resources.length < (currentCharacter.value?.resourceCapacity || 0);
});

// Game actions
const resolveChallenge = () => {
  if (!canResolveChallenge.value || !gameStore.currentChallenge) return;
  
  const successThreshold = 0.6; // 60% chance of success
  const success = Math.random() > (1 - successThreshold);
  
  if (success) {
    gameStore.addToGameLog(`Successfully resolved the challenge!`, true);
    gameStore.setCurrentChallenge(null);
    // Add rewards
    playerStore.healHealth(10);
  } else {
    gameStore.addToGameLog(`Failed to resolve the challenge. Lost health.`, true);
    // Apply penalties
    playerStore.loseHealth(15);
    gameStore.addThreatTokens(1);
  }
};

const gatherResource = (resource: ResourceCard) => {
  if (!canGatherResources.value) return;
  
  playerStore.addResource(resource.id);
  gameStore.addToGameLog(`Gathered ${resource.name}`, false);
};

const nextPhase = () => {
  gameStore.advancePhase();
};

const endJourney = () => {
  if (confirm('Are you sure you want to end your journey? All progress will be lost.')) {
    gameStore.resetGame();
    router.push('/');
  }
};

// Helper functions
const formatSeason = (season: Season): string => {
  return gameStore.formatSeason(season);
};
</script>

